import SplitText from "@/components/motion/SplitText";
import Image from "next/image";
import { Metadata } from "next";

export const metadata: Metadata = {
  title: "Services - Qalby Muslim",
  description: "Discover Zabiha Halal Restaurants Near You and explore our featured halal dining options.",
};

export default function ServicesPage() {
  const restaurants = [
    {
      name: "Restaurant 1",
      description: "Authentic halal cuisine.",
      image: "https://images.unsplash.com/photo-1702550509680-2cafda0a6c68?auto=format&fit=crop&w=656&h=480",
    },
    {
      name: "Restaurant 2",
      description: "Family-friendly dining.",
      image: "https://images.unsplash.com/photo-1682423199837-e1f233a13de0?auto=format&fit=crop&w=656&h=480",
    },
    {
      name: "Restaurant 3",
      description: "Spicy and flavorful.",
      image: "https://images.unsplash.com/photo-1691089853086-c59a93de174f?auto=format&fit=crop&w=656&h=480",
    },
    {
      name: "Restaurant 4",
      description: "Quick service meals.",
      image: "https://images.unsplash.com/photo-1584031745084-223095abdb0c?auto=format&fit=crop&w=656&h=480",
    },
  ];

  const gallery = [
    "https://images.unsplash.com/photo-1672723519075-d55516a22ecc?auto=format&fit=crop&w=316&h=336",
    "https://images.unsplash.com/photo-1698599560806-d7ad43291517?auto=format&fit=crop&w=316&h=336",
    "https://images.unsplash.com/photo-1698580524915-97b31d01823e?auto=format&fit=crop&w=316&h=336",
    "https://images.unsplash.com/photo-1541750071-3a9e91998ab6?auto=format&fit=crop&w=316&h=336",
    "https://images.unsplash.com/photo-1674535083154-504f0ba23144?auto=format&fit=crop&w=316&h=336",
    "https://images.unsplash.com/photo-1558509927-ca362f7b77be?auto=format&fit=crop&w=316&h=336",
  ];

  return (
    <main className="container-max py-24">
      <div className="text-center mb-16">
        <SplitText
          as="h1"
          className="font-heading text-4xl md:text-5xl text-emerald"
          text="Halal Eats"
        />
        <p className="mt-4 text-emerald/80 text-lg">
          Discover Zabiha Halal Restaurants Near You
        </p>
      </div>

      <section className="mb-16">
        <div className="max-w-4xl mx-auto text-center">
          <h2 className="font-heading text-3xl text-emerald mb-8">About Halal Restaurants Near You</h2>
          <p className="text-emerald/80 text-lg mb-8">
            Discover Zabiha Halal dining options near you.
          </p>
          <div className="grid md:grid-cols-2 gap-8 mb-8">
            <div className="text-center">
              <div className="text-4xl font-bold text-emerald mb-2">150+</div>
              <p className="text-emerald/80">Trusted</p>
            </div>
            <div className="text-center">
              <div className="text-4xl font-bold text-emerald mb-2">15</div>
              <p className="text-emerald/80">Quality</p>
            </div>
          </div>
          <a
            href="https://tally.so/r/mKEDQk"
            target="_blank"
            className="inline-block px-6 py-3 bg-emerald text-white rounded-lg hover:bg-emerald/90 transition-colors"
          >
            Add Your Restaurant
          </a>
        </div>
      </section>

      <section className="mb-16">
        <h2 className="font-heading text-3xl text-emerald mb-8 text-center">Gallery</h2>
        <p className="text-center text-emerald/80 mb-8">Explore our featured halal restaurants.</p>
        <div className="grid md:grid-cols-3 gap-6">
          {gallery.map((image, index) => (
            <div key={index} className="relative h-48 rounded-lg overflow-hidden">
              <Image
                src={image}
                alt={`Halal restaurant ${index + 1}`}
                fill
                className="object-cover"
              />
            </div>
          ))}
        </div>
      </section>

      <section className="mb-16">
        <h2 className="font-heading text-3xl text-emerald mb-8 text-center">Reviews</h2>
        <p className="text-center text-emerald/80 mb-8">What our customers say</p>
        <div className="grid md:grid-cols-2 gap-8">
          <div className="bg-white/5 backdrop-blur-sm border border-emerald/20 rounded-lg p-6">
            <p className="text-emerald/80 italic mb-4">"The food quality is exceptional, and the service is always friendly!"</p>
            <div className="flex items-center justify-between">
              <div>
                <p className="font-semibold text-emerald">Ali</p>
                <p className="text-emerald/60">NY</p>
              </div>
              <div className="text-yellow-400">★★★★★</div>
            </div>
          </div>
          <div className="bg-white/5 backdrop-blur-sm border border-emerald/20 rounded-lg p-6">
            <p className="text-emerald/80 italic mb-4">"I love the variety of halal options available. Highly recommend!"</p>
            <div className="flex items-center justify-between">
              <div>
                <p className="font-semibold text-emerald">Sara</p>
                <p className="text-emerald/60">LA</p>
              </div>
              <div className="text-yellow-400">★★★★★</div>
            </div>
          </div>
        </div>
      </section>

      <section className="mb-16">
        <h2 className="font-heading text-3xl text-emerald mb-8 text-center">Projects</h2>
        <p className="text-center text-emerald/80 mb-8">Explore our featured restaurants.</p>
        <div className="grid md:grid-cols-2 gap-8">
          {restaurants.map((restaurant, index) => (
            <div key={index} className="bg-white/5 backdrop-blur-sm border border-emerald/20 rounded-lg overflow-hidden">
              <div className="relative h-48">
                <Image
                  src={restaurant.image}
                  alt={restaurant.name}
                  fill
                  className="object-cover"
                />
              </div>
              <div className="p-6">
                <h3 className="font-heading text-xl text-emerald mb-2">{restaurant.name}</h3>
                <p className="text-emerald/80">{restaurant.description}</p>
              </div>
            </div>
          ))}
        </div>
      </section>

      <section>
        <h2 className="font-heading text-3xl text-emerald mb-8 text-center">Contact Us</h2>
        <div className="max-w-2xl mx-auto">
          <p className="text-center text-emerald/80 mb-8">
            Reach out to us for any inquiries or to add your restaurant.
          </p>
          <div className="grid md:grid-cols-2 gap-6 mb-6">
            <div>
              <h3 className="font-semibold text-emerald mb-2">Phone</h3>
              <p className="text-emerald/80">12345</p>
            </div>
            <div>
              <h3 className="font-semibold text-emerald mb-2">Email</h3>
              <p className="text-emerald/80">info@example.com</p>
            </div>
          </div>
          <form className="space-y-6" action="mailto:qalbymuslim1@gmail.com" method="post" encType="text/plain">
            <div className="grid md:grid-cols-2 gap-6">
              <input
                type="text"
                name="restaurantName"
                placeholder="Restaurant Name"
                className="w-full px-4 py-3 bg-white/5 border border-emerald/20 rounded-lg text-emerald placeholder-emerald/50 focus:outline-none focus:border-emerald"
                required
              />
              <input
                type="text"
                name="location"
                placeholder="Location"
                className="w-full px-4 py-3 bg-white/5 border border-emerald/20 rounded-lg text-emerald placeholder-emerald/50 focus:outline-none focus:border-emerald"
                required
              />
            </div>
            <input
              type="text"
              name="contactInfo"
              placeholder="Contact Info"
              className="w-full px-4 py-3 bg-white/5 border border-emerald/20 rounded-lg text-emerald placeholder-emerald/50 focus:outline-none focus:border-emerald"
              required
            />
            <button
              type="submit"
              className="w-full px-6 py-3 bg-emerald text-white rounded-lg hover:bg-emerald/90 transition-colors"
            >
              Submit
            </button>
          </form>
        </div>
      </section>
    </main>
  );
}